import { createSlice } from "@reduxjs/toolkit";

export const myCollections = createSlice({
    name : "myCollections",
    initialState: {
        searchQuery: [],
        collectionsArray: [],
    },
    reducers:{
        //Definiera de olika reducers som vi vill anvnda, och deras logik.
        //vi behver inte definiera actions fr dessa reducers eftersom de r gjorda automatiskt av redux-toolkit.
        /**
         * Nr vi anvnder redux toolkit s anvnds immer under the hood, s vi fr skriva "mutable" logik hr men ENDAST hr inne.
         * Med react-redux 
         */ //Example of reducers.
        setSearchQuery: (state, action) => { //Kanske inte behvs ifall vi anvnder local?
            state.searchQuery = action.payload;
        },
        setCollectionsArray: (state, action) => {
            console.log("This is the payload", action.payload);
            state.collectionsArray = action.payload;
            console.log("This is the collections array after storing", state.collectionsArray);
        },
        
    }
})


export const { setSearchQuery, setCollectionsArray } = myCollections.actions;

